import * as api from '@/axios/api';


export default function record_activity (actionid, objectid,response) {
    let uid = 'users'
    let body = {
        action: actionid,
        objectid: objectid,
        meta: JSON.stringify(response),
    }
    api.activitiesApi(uid, body).then(response => {console.log(response)
    }).catch(error =>{console.log(response)})
};
