import React from 'react';
import { Spin } from "antd";
import OperationType from '@/constant/OperationType'
import ConvertForms from '@/constant/config';
import ConvertType from '@/constant/ConvertType';
import config from '@/axios/config';
import { DndProvider } from 'react-dnd';
import FactCard from '../VideoSliceCardList/FactCard';
import { genFactSentence, genSubtitle } from '@/sentencer/index';
import activitiesCode from '@/axios/activitiesCode';
import record_activity from '@/axios/recordactivities';
import * as api from '@/axios/api';
import Fact from '@/model/fact';
import uuidv4 from 'uuid/v4';
import { filter } from 'lodash';
import { HTML5Backend } from 'react-dnd-html5-backend';
const addFactNum = 10;

export default class DataFact extends React.Component {
    constructor(props) {
        super(props)
        const { intl} = this.props
        let lang = intl.options.currentLocale
        filter(lang);
        this.state = {};
    }

    UNSAFE_componentWillMount() {
        const { unusedFacts } = this.props;
        if (!unusedFacts.length) {
            this.addFact();
        }
        
    }
    getGenerateURL = (type) => {
        switch (type) {
            case ConvertForms.DATACOMICS:
                return config.url.generateGraphRandomly;
            case ConvertForms.FACTSHEET:
            default:
                return config.url.generateRandomly;
        }
    }

    addFact = () => {
        let typeForm = ConvertForms[0];
        this.setState({
            operateState: OperationType.ADDING
        })
        let type = typeForm.name;
        //TODO:会有重复的fact
        this.adding(this.getGenerateURL(type)).then(response => {
            const facts = response.data.story.facts;

            switch (type) {
                case ConvertType.DATACOMICS:
                case ConvertType.FACTSHEET:
                case ConvertType.DASHBOARD:
                case ConvertType.DATAVIDEOS:
                default:
                    let tempFacts = [];
                    for (let factDict of facts) {
                        let fact = new Fact(
                            factDict['type'],
                            factDict['measure'],
                            factDict['subspace'],
                            factDict['groupby'],
                            factDict['focus'],
                            factDict['parameter'],
                            "", // chart
                            factDict['score'],
                            factDict['information'],
                            factDict['significance']
                        );
                        fact.uuid = uuidv4();
                        tempFacts.push(fact);
                    }
                    this.props.addFact(tempFacts);
                    break;
            }
        });
    }

    adding = (url) => {
        return new Promise(async (resolve, reject) => {
            let fileName = this.props.fileName;
            let filters = _.cloneDeep(this.props.filtersLocal);

            let data;
            if (filters)
                data = {
                    file_name: fileName,
                    max_story_length: addFactNum,
                    filters: JSON.stringify(filters),
                    method: 'mcts'
                }

            const response = await api.generate(url, data)
            console.log('response-adding', response)
            if (response.data.fail) {
                this.setState({
                    operateState: OperationType.ADD_FAILED,
                    errorMessage: response.data.fail
                })
                reject()
                record_activity(activitiesCode.generateMoreFacts, 0, { filename: fileName, response: response });
            }
            if (response.data.error) {
                this.setState({
                    operateState: OperationType.ADD_FAILED,
                    errorMessage: response.data.error
                })
                reject()
                record_activity(activitiesCode.generateMoreFacts, 0, { filename: fileName, response: response });
            }
            this.setState({
                operateState: OperationType.GENERATED
            })
            resolve(response)
            record_activity(activitiesCode.generateMoreFacts, 1, { filename: fileName, response: response });
        })
    }
    
    render() {
        const { intl, initDone, unusedFacts } = this.props
        const { operateState } = this.state
        let lang = intl.options.currentLocale
        let tip = initDone && intl.get("adding");
        unusedFacts.map((item, index) => {
            item.generatedScript = genFactSentence(item, lang)
            item.generatedSubtitle = genSubtitle(item, lang)
        })
        //添加fact的组件
        const AddView = () => {
            return (
                <div className='add'>
                    <div className="addImage" onClick={this.addFact}></div>
                </div>
            )
        }
        return (
            <DndProvider backend={HTML5Backend} options={{ enableMouseEvents: true }}>
                <div className='Topic-Facts'>
                    <div className="DataFacts">
                        <Spin tip={tip} spinning={operateState === OperationType.ADDING} wrapperClassName="editSpining">
                            {/* <DataFactsTxt /> */}
                            <div className='factsList'>
                                <ul>
                                    {
                                        unusedFacts.map((fact, index) => {
                                            return (
                                                <div className="factCardOuterFrame">
                                                    <FactCard width={135} height={86} key={fact.uuid + fact.index} dataresLocal={this.props.dataresLocal} schemaFilterLocal={this.props.schemaFilterLocal}  {...this.props} fact={fact} index={index} id={fact.uuid} isReady={true} />
                                                </div>
                                            )
                                        })
                                    }
                                </ul>
                            </div>
                        </Spin>
                    </div>
                    <div>
                        <AddView />
                    </div>
                </div>
            </DndProvider>
        )
    }
}