import ActionType from './type';


export const initialVideoSlice = (facts, schemaFilter, filename, datares, schema) => ({
    type: ActionType.INIT_VIDOE_CARD,
    facts,
    schemaFilter,
    filename,
    datares,
    schema
})

export const replayVideoSlice = () => ({
    type: ActionType.REPLAY_VIDEO_SLICE
})

export const playVideoSlice = (index) => ({
    type: ActionType.PLAY_VIDEO_SLICE,
    index
})

//play video continuity
export const playVideoContinue = () => ({
    type: ActionType.PLAY_VIDEO_CONTINUE
    
})

//stop play video continuity
export const stopPlayVideoContinue = () => ({
    type: ActionType.STOP_PLAY_VIDEO_CONTINUE
})

//add nowVideoTime
export const addNowVideoTime = () => ({
    type: ActionType.ADD_NOW_VIDEO_TIME
})

export const changeNowVideoTime = (time) => ({
    type: ActionType.CHANGE_NOW_VIDEO_TIME,
    time,
})

export const changeAllVideoTime = (time) => ({
    type: ActionType.CHANGE_ALL_VIDEO_TIME,
    time,
})

export const removeCard = (index) => ({
    type: ActionType.REMOVE_CARD,
    index
})

//drag card to another index
export const changeCard = (origin ,target) => ({
    type: ActionType.CHANGE_CARD,
    origin,
    target    
})

export const exchangeCard = (index,card) => ({
    type: ActionType.EXCHANGE_CARD,
    index,
    card
})

//drag data fact to card list
export const addCard = (fact,specs,time,isGeneratedByAlg,insert) => ({
    type:ActionType.ADD_CARD,
    fact,specs,time,isGeneratedByAlg,
    insert
})

export const addCards = (cards,insert) => ({
    type:ActionType.ADD_CARDS,
    cards,
    insert
})

export const changeCardFlag = (index,flag) => ({
    type:ActionType.CHANGE_CARD_FLAG,
    index,
    flag,
})



//add empty slice
export const addEmptyCard = () => ({
    type: ActionType.ADD_EMPTY_CARD
})

//set leftsider content
export const setLeftsiderContent = (content) => ({
    type: ActionType.SET_LEFTSIDER_CONTENT,
    content
})


