import React from 'react';
import { fact2unitVis } from '../../../../tool/fact2unitVis';
import { fact2chart } from '../../../../tool/fact2vis.js';
import uuidv4 from 'uuid/v4';
import ChartType from '../../../../constant/ChartType';
import getSupportedChartTypes from '@/tool/getSupportedChartTypes';
import _ from 'lodash';
import { isValid, customizeFact } from '@/view/FactEditor/helper';
import { DragSource } from 'react-dnd';
import { factColors } from '../../../../view/StoryView/Color.js'
import { Tooltip } from 'antd';
import activitiesCode from '@/axios/activitiesCode';
import record_activity from '@/axios/recordactivities'
import * as d3 from 'd3';
import { addEmptyCard } from '../../../../action/videoAction';
const Types = {
    CARD: 'card'
}
export const getChoice = (length) => {
    let choice = Math.round(Math.random() * (length - 1));
    return choice;
}

export const getFactChartType = (shema, fact, choice = 0) => {
    let supportedChartTypes = getSupportedChartTypes(fact, shema);
    if (supportedChartTypes.length === 0) return null;
    return supportedChartTypes[getChoice(supportedChartTypes.length)].chart;
}
/**
 * Specifies the drag source contract.
 * Only `beginDrag` function is required.
 */
const cardSource = {
    canDrag(props) {
        // You can disallow drag based on props
        return props.isReady
    },

    isDragging(props, monitor) {
        // If your component gets unmounted while dragged
        // (like a card in Kanban board dragged between lists)
        // you can implement something like this to keep its
        // appearance dragged:
        return monitor.getItem().id === props.id
    },

    beginDrag(props, monitor, component) {
        // Return the data describing the dragged item
        let controls = document.body.querySelectorAll(".dragCard");
        controls[component.props.index].classList.add("dragging");
        const item = { id: props.id }
        return item
    },

    endDrag(props, monitor, component) {
        if(props.isPlayVideo)
        {
            null
        }
        else
        {
            let controls = document.body.querySelectorAll(".dragCard");
            controls[component.props.index].classList.remove("dragging");

            if (!monitor.didDrop()) {
                // You can check whether the drop was successful
                // or if the drag ended but nobody handled the drop
                return
            }

            const dropResult = monitor.getDropResult();
            let { addCard, index } = component.props;
            if (typeof (dropResult.insert) !== "undefined") {
                //record_activity(activitiesCode.dragNewFact, 1, props.fact)
                let schemaFilter = _.cloneDeep(props.schemaFilterLocal);
                let schema=props.schema
                let specData
                schemaFilter ? specData = { "schema": schemaFilter, } : specData = { "schema": schema };

                let fact = props.fact

                if (!fact.chart || fact.chart === "") {
                    fact.chart = getFactChartType(specData.schema, fact); //针对生成页面的chart
                }
            
                let chart = fact.chart;
            
                if (chart === ChartType.ISOTYPE_BAR_CHART) {
                    fact.chart = ChartType.VERTICAL_BAR_CHART;
                }
                fact = customizeFact(fact);
                if (!fact.chart || !isValid(fact)) {
                    console.log("no valid", fact)
                    return null;
                }

                let specFact = {
                    ...fact,
                    breakdown: fact.groupby.map(d => {
                        return {
                            "field": d
                        }
                    })
                }

                let width_height = { "width": 135, "height": 86 };
                let size = null;
                let specChart;
                if (width_height) {
                    specChart = {
                        width: width_height.width,
                        height: width_height.height,
                        type: "unitvis",
                        style: "business",
                        duration: 0,
                        showSuggestion: true, //显示图表Unsupported
                    }
                } else {
                    specChart = {
                        size: size,
                        type: "unitvis",
                        style: "business",
                        duration: 0,
                        showSuggestion: true, //显示图表Unsupported
                    }
                }

                let datares = _.cloneDeep(props.dataresLocal);

                specData = {
                    ...specData,
                    values: datares,
                }

                let specNew = {
                    data: specData,
                    fact: specFact,
                    chart: specChart,
                }

                let insertFact = props.fact
                let specs = specNew
                let time = 6000;
                // 加入facts中
                record_activity(activitiesCode.dragNewFact, 1 , props.fact)
                props.insertFact(index, dropResult.insert);
                // 添加卡片到下方keyframes中
                addCard(insertFact,specs,time,false, dropResult.insert);
                // 删除侧边栏中当前卡片
                props.deleteUnusedFact(index);
                // 添加对应的time控制的bar
                let bar = { leftbar: 140, rightbar: 18,isDrag:false }
                props.addBarToTimeLineBar(bar,dropResult.insert)
            }
        }
    }
}

/**
 * Specifies which props to inject into your component.
 */
function collect(connect, monitor) {
    return {
        connectDragSource: connect.dragSource(),
        // You can ask the monitor about the current drag state:
        isDragging: monitor.isDragging()
    }
}


class FactCard extends React.Component {

    deleteFact = () => {
        this.props.deleteUnusedFact(this.props.index);
    }

    shouldComponentUpdate(nextProps) {
        let { fact } = this.props;
        let nextFact = nextProps.fact;
        return !_.isEqual(fact, nextFact);
    }

    render() {
        let { data, schema, fact, isReady, filters,width,height } = this.props;

        let datares = _.cloneDeep(this.props.dataresLocal);
        // console.log(datares)


        let schemaFilter = _.cloneDeep(this.props.schemaFilterLocal);

        let specData
        if (schemaFilter) {
            specData = {
                "schema": schemaFilter,
            };
        }
        else {
            specData = {
                "schema": schema
            };
        }
        let uuid = uuidv4();
        // 接入 unit vis 图表
        let vis = fact2unitVis(specData, uuid, fact, datares,null,{"width":width,"height": height} ,true);
        // These props are injected by React DnD,
        // as defined by your `collect` function above:
        const { connectDragSource } = this.props;
        return connectDragSource(
            <li>
                <div className={isReady ? "factCard dragCard" : "factCard"} >
                    <div className='delete' onClick={() => this.deleteFact()}></div>
                    <div className='vis'>
                        {vis}
                    </div>
                </div>
            </li>
        )
    }
}

export default DragSource(Types.CARD, cardSource, collect)(FactCard);
